  }

  .CompetitorPlaybookFrame-What-first-section {
    padding : 20px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;

  }
  
  /* Emotion Insights */
  .CompetitorPlaybookFrame-What-emotions {
    display: flex;
    gap: 10px;
    min-width: 590px;
    max-width: 590px;
    width: 490px;
  }
  
  .CompetitorPlaybookFrame-What-emotion-cards-left {
    gap: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
 }
  .CompetitorPlaybookFrame-What-emotion-card-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .CompetitorPlaybookFrame-What-emotion-card {
    background: white;
    padding: 14px;
    border-radius: 14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    position: relative;
    width: 256px;
    height: 86px;
    border: 2px #E5E5E5 solid;
  }
  
  .CompetitorPlaybookFrame-What-emotion-card h4 {
    font-size: 14px;
