}

.CompetitorPlaybookFrame-How-highlights ul {
    list-style: none;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.CompetitorPlaybookFrame-How-highlights li {
    font-size: 16px;
    color: #000;
    position: relative;
    font-weight: bold;
    margin: 0;
    padding-left: 15px;
}

.CompetitorPlaybookFrame-How-highlights li::before {
    content: ""; /* Bullet character */
    font-size: 22px; /* Increase bullet size */
    color: black; /* Bullet color */
    position: absolute;
    left: 0;
    top: 0;
}

/* Bottom Box */
.CompetitorPlaybookFrame-Why-bottom {
    margin-top: 20px;
}

.CompetitorPlaybookFrame-Why-box {
