:root {
  --emotional: linear-gradient(95.27deg, #FD6767 28.12%, #F32E7C 67.9%, #ED018B 107.68%);
  --bio_optimizer:linear-gradient(114.52deg, #A6FFCA 12.46%, #35DEFA 87.58%);
  --trinity: linear-gradient(107.71deg, #8E2DE2 -1.21%, #495CC8 49.91%, #0B06FA 101.02%);
  --horizon:linear-gradient(101.47deg, #FF416C 15.22%, #FF4B2B 59.37%, #F67963 103.51%);

  --reflection:linear-gradient(111.7deg, #36D1DC -3.97%, #5B86E5 97.31%);
  --derivative: linear-gradient(90deg, #C61956 0%, #9379C4 79.21%, #1C95E5 142.07%, #0887D2 186.38%);

  --competitor: linear-gradient(105.84deg, #000080 21.33%, #5B5858 81.92%);
  --competitor1:linear-gradient(111.7deg, #36D1DC -3.97%, #5B86E5 97.31%);
  --playbook: linear-gradient(90deg, #E57B15 0%, #D54989 100%);
  --audience: linear-gradient(98.66deg, rgba(0, 201, 255, 0.5) 2.28%, rgba(76, 175, 80, 0.5) 52.78%),
linear-gradient(76.78deg, rgba(114, 127, 227, 0.5) 5.97%, rgba(122, 229, 130, 0.5) 74.91%);
--competitor_tracker:linear-gradient(105.84deg, #000080 21.33%, #5B5858 81.92%);

