            background-color: black;
            position: absolute;
            border-radius: 20px;
            border: 1px solid #E5E5E5;
        }

        .trinity_card_1 {
            top: 0;
            left: 0;
            z-index: 1;
        }

        .trinity_card_2 {
            top: 65px;  /* 1/3 of card height */
            left: 56px; /* 1/3 of card width */
            opacity: 0.85;
            z-index: 2;
        }

        .trinity_card_3 {
            top: 140px;  /* 2/3 of card height */
            left: 105px; /* 2/3 of card width */
            opacity: 0.75;
            z-index: 3;
        }

        .trinity_card_1::before,
        .trinity_card_2::before,
        .trinity_card_3::before {
            content: attr(data-number);
            position: absolute;
            top: -20px;
            right: 10px;
            font-size: 18px;
            font-weight: bold;
            font-family: Arial, sans-serif;
            color: black;
            text-shadow: 1px 1px 2px rgba(255, 255, 255, 0.5);
        }
        
