.CompetitorPlaybookFrame-What-Graph-container {
  margin: auto;
  height: 100%;
  width: 450px;
}

.CompetitorPlaybookFrame-What-Graph-filters {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
}

.CompetitorPlaybookFrame-What-Graph-filter-btn {
  border: 2px solid #00A4FF;
  background: white;
  color: #00A4FF;
  border-radius: 20px;
  font-size: 14px;
  cursor: pointer;
  transition: 0.3s;
  width: 140px;
  height: 30px;
}

.CompetitorPlaybookFrame-What-Graph-filter-btn.active,
.CompetitorPlaybookFrame-What-Graph-filter-btn:hover {
  background: #00A4FF;
  color: white;
}

.CompetitorPlaybookFrame-What-Graph-chart {
  height: 400px;
  width: auto;
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
}
