.heatmap-container {
    width: 100%;
    padding: 20px;
    background-color: #f8f8f8;
}

/* Header Section */
.Chronological-heatmap-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.Chronological-heatmap-logo {
    width: 23px;
    height: 21px;
}

.Chronological-heatmap-image-title {
    display: flex;
}


.Chronological-heatmap-header-image {
    display: flex;
    flex-direction: row;
    align-items: end;
    gap: 5px;
    margin-left: 20px;
}

.Chronological-heatmap-header-image-text {
    color: black;
    font-size: 12px;
    font-family: Montserrat;
    font-weight: 600;
    word-wrap: break-word;
    margin: 0;
}

.Chronological-heatmap-section {
    text-align: center;
}

.Chronological-heatmap-title {
    font-size: 22px;
    font-weight: bold;
    color: gray;
}

.Chronological-heatmap-title.active {
    color: #0B66FF;
}

.Chronological-heatmap-buttons {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 10px;
}

.Chronological-heatmap-button {
    padding: 8px 16px;
    font-size: 14px;
    border-radius: 20px;
    border: 1px solid #00A4FF;
    background: white;
    cursor: pointer;
    color: #00A4FF;
    width: 150px;
    height: 30px;
}

.Chronological-heatmap-button.active {
    background: #00A4FF;
    color: white;
}

.Chronological-heatmap-header-row {
    display: flex;
    justify-content: space-between;
}

.Chronological-heatmap-row {
    display: flex;
    justify-content: space-between;
}

.Chronological-heatmap-cell {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    color: black;
    border-radius: 4px;
}

.Chronological-heatmap-cell-header {
    font-weight: bold;
    background: white;
}

/* Footer */
.Chronological-heatmap-footer {
    text-align: center;
    margin-top: 40px;
}

.Chronological-heatmap-footer-button {
    padding: 0px 40px 0px 40px;
    background: white;
    color: #0070FF;
    border: 1px #0070FF solid;
    border-radius: 20px;
    cursor: pointer;
    height: 30px;
}

.Chronological-heatmap-footer-text {
    margin-top: 10px;
    font-size: 14px;
    color: #0B66FF;
}
