@font-face {
    font-family: 'EloquiaDisplay';
    src: url('../../../public/assets/fonts/eloquia-display-semibold.ttf') format('opentype');
    font-weight: 800;
    /* Semi Bold */
    font-style: normal;
}

@font-face {
    font-family: 'EloquiaDisplay';
    src: url('../../../public/assets/fonts/Typekiln\ -\ EloquiaText-ExtraLight.otf') format('opentype');
    font-weight: 200;
    /* Extra Light */
    font-style: normal;
}

.PricingComparaison-header-title {
    margin: 0;
    background: linear-gradient(117.96deg, #0B66FF 0%, #00A4FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 42px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word
}

.PricingComparaison-header-text {
    margin: 0;
    color: #777777;
    font-size: 20px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    line-height: 18px;
    word-wrap: break-word
}

.PricingComparaison {
    width: 80%;
    margin: 30px auto;
}


.most-populaire {
    width:100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.most-populaire-container {
    display: flex;
    height: 30px;
}

.most-populaire-button {
    cursor: pointer;
    border: 1px solid #007bff;
    border-radius: 50px;
    color: #007bff;
    background-color: white;
    width: 150px;
}


.PricingComparaison-icon {
    font-size: 48px;
}

.PricingComparaison-items-brainicon {
    width: 65px;
    height: 65px;
}

.PricingComparaison-items-Elevate-header {
    background: black;
    border-radius: 14px;
}

.PricingComparaison-items-header {
    width: 135px;
    height: 86px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 30px;
}

.PricingComparaison-items-Elevate-header-title {
    color: #C4D5DF;
    font-size: 32px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word;
    margin: 0;
    background: linear-gradient(117.96deg, #C4D5DF 0%, #849DAB 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    padding-top: 30px;
}

.PricingComparaison-items:not(.active-price) {
    margin-top: 58px;
}

.active-price .PricingComparaison-items-header {
    padding: 13px;
}

.PricingComparaison-items-Elevate-header-text {
    margin: 0;
    color: white;
    font-size: 12px;
    font-family: 'EloquiaDisplay';
    font-weight: 400;
    word-wrap: break-word
}

.PricingComparaison-items-number {
    color: black;
    font-size: 42px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word;
    margin: 0;
}

.PricingComparaison-items-text-elements {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.PricingComparaison-items-text-elements p {
    margin: 0;
    color: #242424;
    font-size: 14px;
    font-family: 'EloquiaDisplay';
    font-weight: 300;
    line-height: 38px;
    word-wrap: break-word
}

.PricingComparaison-items-live {
    margin: 0;
    color: #0B66FF;
    font-size: 32px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    line-height: 38px;
    word-wrap: break-word
}

.PricingComparaison-items-focus-mode {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.PricingComparaison-items-focus {
    margin: 0;
    color: black;
    font-size: 32px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    line-height: 25px;
    word-wrap: break-word
}

.PricingComparaison-items-mode {
    margin: 0;
    color: black;
    font-size: 32px;
    font-family: 'EloquiaDisplay';
    font-weight: 400;
    line-height: 25px;
    word-wrap: break-word
}


.active-price {
    background: rgba(166, 219, 255, 0.17);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.10);
    border-radius: 20px;
    border: 1px #E5E5E5 solid;
}


.PricingComparaison-container-pricing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.PricingComparaison-container-pricing-title {
    margin: 0;
    color: #63637E;
    font-size: 16px;
    font-family: 'EloquiaDisplay';
    font-weight: 500;
    word-wrap: break-word;
    margin-top: 30px;
}

.PricingComparaison-container-pricing-text {
    margin: 0;
    color: #242424;
    font-size: 16px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word
}

.PricingComparaison-container-text-features {
    margin: 0;
    color: #0B66FF;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    text-decoration: underline;
    word-wrap: break-word
}

.PricingComparaison-container-button {
    padding: 10px;
    background: #0B66FF;
    border-radius: 20px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    width: 153px;
    text-align: center;
    color: white;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word;
    border: none;
    height: 30px;
}

.PricingComparaison-items {
    width: 256px;
    height: 3201px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    gap: 30px;
}

.PricingComparaison-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    gap: 25px;
}

.PricingComparaison-items-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 120px;
}


.PricingComparaison-items-Core-header-title {
    margin: 0;
    font-size: 32px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word;
    background: linear-gradient(117.96deg, #EF4C2D 0%, #CD52BC 50%, #6475F3 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-top: 30px;
}

.PricingComparaison-items-Core-header-text {
    margin: 0;
    color: #242424;
    font-size: 12px;
    font-family: 'EloquiaDisplay';
    font-weight: 400;
    word-wrap: break-word
}

.PricingComparaison-items-Evolve-header-title {
    margin: 0;
    color: #94C5F4;
    font-size: 32px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word;
    background: linear-gradient(117.96deg, #94C5F4 0%, #89CBEB 50%, #60DEE3 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}

.PricingComparaison-items-Evolve-header-text {
    margin: 0;
    color: #242424;
    font-size: 12px;
    font-family: 'EloquiaDisplay';
    font-weight: 400;
    word-wrap: break-word
}


.PricingComparaison-items-Basic-header-title {
    margin: 0;
    font-size: 32px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word;
    color: #777777;
    margin-top: 60px;
}

.PricingComparaison-items-Basic-header-text {
    margin: 0;
    color: #86868B;
    font-size: 12px;
    font-family: 'EloquiaDisplay';
    font-weight: 400;
    word-wrap: break-word
}

.PricingComparaison-container-pricing-text-Free {
    margin: 0;
    color: #0B66FF;
    font-size: 16px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word
}


.PricingComparaison-footer p {
    margin: 0;
    color: #242424;
    font-size: 12px;
    font-family: Lato;
    font-weight: 400;
    word-wrap: break-word
}

.PricingComparaison-footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin: 70px 0;
}

.PricingComparaison-footer-button-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 30px;
 }

.PricingComparaison-footer-button-container-light {
    padding: 10px;
    border-radius: 20px;
    border: 1px #00A4FF solid;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    color: #00A4FF;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word;
    width: 163px;
    height: 30px;
}

.PricingComparaison-footer-button-container-blue {
    padding: 10px;
    background: #00A4FF;
    border-radius: 20px;
    border: 1px #00A4FF solid;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    color: white;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word;
    width: 163px;
    height: 30px;
}



/* Mobile Optimization Styles */
@media (max-width: 768px) {
    .PricingComparaison {
        width: 90%;
    }

    .PricingComparaison-header-title {
        font-size: 32px;
    }

    .PricingComparaison-header-text {
        font-size: 16px;
    }

    .PricingComparaison-items-container {
        flex-direction: column;
        align-items: center;
        gap: 20px;
    }

    .PricingComparaison-container {
        width: 100%;
        align-items: center;
    }

    .PricingComparaison-items {
        width: 100%;
        height: auto;
        gap: 20px;
    }

    .PricingComparaison-items-header {
        width: auto;
        padding: 10px;
    }

    .PricingComparaison-items-Elevate-header-title {
        font-size: 24px;
    }

    .PricingComparaison-items-Elevate-header-text {
        font-size: 10px;
    }

    .PricingComparaison-items-number {
        font-size: 32px;
    }

    .PricingComparaison-items-text-elements p {
        font-size: 12px;
        line-height: 30px;
    }

    .PricingComparaison-items-live {
        font-size: 24px;
    }

    .PricingComparaison-items-focus {
        font-size: 24px;
    }

    .PricingComparaison-items-mode {
        font-size: 24px;
    }

    .PricingComparaison-icon {
        font-size: 36px;
    }

    .PricingComparaison-items-brainicon {
        width: 50px;
        height: 50px;
    }

    .PricingComparaison-container-pricing {
        flex-direction: row;
        gap: 10px;
    }

    .PricingComparaison-container-pricing-title {
        font-size: 14px;
    }

    .PricingComparaison-container-pricing-text {
        font-size: 14px;
    }

    .PricingComparaison-container-text-features {
        font-size: 10px;
    }

    .PricingComparaison-container-button {
        width: 100%;
        font-size: 14px;
    }

    .PricingComparaison-footer {
        gap: 10px;
    }

    .PricingComparaison-footer-button-container {
        flex-direction: column;
        gap: 10px;
    }

    .PricingComparaison-footer-button-container-light,
    .PricingComparaison-footer-button-container-blue {
        width: 100%;
        font-size: 12px;
    }

    .PricingComparaison-footer p {
        font-size: 10px;
    }
}
