@font-face {
    font-family: 'EloquiaDisplay';
    src: url('../../../public/assets/fonts/eloquia-display-semibold.ttf') format('opentype');
    font-weight: 800;
    /* Semi Bold */
    font-style: normal;
}

@font-face {
    font-family: 'EloquiaDisplay';
    src: url('../../../public/assets/fonts/Typekiln\ -\ EloquiaText-ExtraLight.otf') format('opentype');
    font-weight: 200;
    /* Extra Light */
    font-style: normal;
}

.PerformiaStart {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    height: 100vh;
}


.PerformiaStart-title {
    font-size: 32px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word;
    background: linear-gradient(117.96deg, #F7971E 0%, #FFD200 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 0;
}

.PerformiaStart-button {
    padding: 10px;
    background: #0070FF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    color: white;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word;
    width: 303px;
    border: none;
    cursor: pointer;
}

.PerformiaStart-text {
    color: #0070FF;
    font-size: 12px;
    font-family: Lato;
    font-weight: 400;
    word-wrap: break-word;
    margin: 0;
}

.Performia {
    margin: 40px auto;
    width: 80%;
}

.Performia-header {
    display: flex;
    flex-direction: column;
}

.Performia-header-title {
    margin: 0;
    font-size: 32px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word;
    background: linear-gradient(to right, #F7971E 0%, #FFD200 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}


.Performia-header-text {
    color: #777777;
    font-size: 20px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    line-height: 18px;
    word-wrap: break-word;
    margin: 0;

}


.Performia-top-button-container-button {
    width: 181px;
    padding: 10px;
    background: white;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    border: 1px #242424 solid;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    color: #242424;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word;
    height: 30px;
}

.Performia-top-button-container-text {
    margin: 0;
    color: #0B66FF;
    font-size: 12px;
    font-family: Lato;
    font-weight: 400;
    word-wrap: break-word
}

.Performia-top-button-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.close-button {
    color: black;
    font-size: 42px;
    font-family: Eloquia Display;
    font-weight: 600;
    word-wrap: break-word;
    background: white;
    border: none;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}

.slick-slide>*:first-child {
    padding: 20px;
    background: white;
    margin: 5px;
    border-radius: 20px;
}

.Performia-graph {
    margin: 40px 0;
}


.Performia-enlarge-title {
    color: #777777;
    font-size: 20px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word;
    margin-bottom: 50px;
    text-align: center;
}


.ReactModal__Content.ReactModal__Content--after-open canvas {
    margin: auto;
    height: 450px !important;
}



.Performia-container-footer-header-image-container {
    display: flex;
    flex-direction: row;
    align-items: center;

}

.Performia-container-footer-header-image-container img {
    width: 23px;
    height: 21px
}

.Performia-container-footer-header-image-container p {
    margin: 0;
    color: black;
    font-size: 12px;
    font-family: Montserrat;
    font-weight: 600;
    word-wrap: break-word
}

.Performia-container-footer-header-title {
    margin: 0;
    color: black;
    font-size: 32px;
    font-family: Eloquia Display;
    font-weight: 600;
    word-wrap: break-word
}

.Performia-container-footer-header-title span {
    color: #0070FF;
}

.Performia-container-footer-header-texte {
    color: #777777;
    font-size: 20px;
    font-family: Eloquia Display;
    font-weight: 600;
    line-height: 18px;
    word-wrap: break-word;
    margin: 0;
    width: 50%;
}


.Performia-container-footer-header {
    display: flex;
    flex-direction: column;
    gap: 2px;
    width: 80%;
    margin: 80px auto;
}

.Performia-container-footer-content-button {
    width: 319px;
    padding: 10px;
    background: #0070FF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    border: 1px #0070FF solid;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    color: white;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word;
    height: 30px;
}


.Performia-container-footer-content-text {

    color: #0070FF;
    font-size: 12px;
    font-family: Lato;
    font-weight: 400;
    word-wrap: break-word;
    text-align: center;
}

.Performia-container-footer-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 40px;
    width: 60%;
    margin-left: auto;
}

.Performia-container-footer {
    background: white;
    box-shadow: 0px 4px 12px rgb(0 0 0);
    border: 1px #E5E5E5 solid;
    height: 500px;
    padding: 80px;
}


.more-data-container {
    text-align: center;
    padding: 20px 20px 70px 20px;
    width: 100%;
    margin: 20px auto;
}
  
.more-data-title {
    font-size: 18px;
    color: #000;
    margin-bottom: 10px;
}
  
.more-data-button {
    background-color: #0070ff;
    color: white; /* White text color */
    font-size: 14px; /* Font size for the button */
    padding: 10px 20px; /* Add padding inside the button */
    border: none; /* Remove the default border */
    border-radius: 20px; /* Round the button corners */
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    cursor: pointer; /* Change cursor to pointer on hover */
    height: 30px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    width: 350px;
}
  
.more-data-button:hover {
    background-color: #0056cc; /* Darker blue on hover */
}
  
.more-data-note {
    font-size: 12px; /* Smaller font size for the note */
    color: #0070FF; /* Blue text color */
    margin-top: 10px; /* Space above the note */
}  



/* Mobile Optimization Styles */
@media (max-width: 768px) {
    .Performia-container {
        width: 90%;
        margin: 20px auto;
    }

    .Performia {
        width: 100%;
    }

    .Performia-header-title {
        font-size: 24px;
    }

    .Performia-header-text {
        font-size: 16px;
    }

    .Performia-top-button-container {
        flex-direction: column;
        align-items: center;
        margin-top: 20px;
    }

    .Performia-top-button-container-button {
        width: 100%;
        font-size: 14px;
    }

    .Performia-top-button-container-text {
        font-size: 14px;
    }

    .Performia-enlarge-title {
        font-size: 16px;
        margin-bottom: 30px;
    }

    .Performia-container-footer-header {
        width: 90%;
        gap: 10px;
    }

    .Performia-container-footer-header-title {
        font-size: 24px;
    }

    .Performia-container-footer-header-texte {
        font-size: 16px;
    }

    .Performia-container-footer-header-image-container {
        flex-direction: column;
        align-items: center;
    }

    .Performia-container-footer-header-image-container img {
        width: 20px;
        height: 20px;
    }

    .Performia-container-footer-header-image-container p {
        font-size: 12px;
    }

    .Performia-container-footer-content {
        width: 90%;
        gap: 20px;
    }

    .Performia-container-footer-content-button {
        width: 100%;
        font-size: 14px;
    }

    .Performia-container-footer-content-text {
        font-size: 14px;
    }

    .Performia-container-footer {
        height: auto;
        padding: 20px;
    }

    .heatmap {
        overflow: scroll;
    }


    .heatmap-image-title {
        flex-direction: column !important;
        gap: 0px !important;
    }

    .metric-buttons {
        margin-bottom: 20px;
        display: flex;
        flex-direction: row;
        gap: 20px !important;
        flex-wrap: wrap;
        align-items: center;
    }
}

@media (min-width: 768px) {
    .Performia-container-footer {
        padding-left: 150px;
    }
}