@font-face {
    font-family: 'EloquiaDisplay';
    src: url('../../public/assets/fonts/eloquia-display-semibold.ttf') format('opentype');
    font-weight: 800;
    /* Semi Bold */
    font-style: normal;
}

@font-face {
    font-family: 'EloquiaDisplay';
    src: url('../../public/assets/fonts/Typekiln\ -\ EloquiaText-ExtraLight.otf') format('opentype');
    font-weight: 200;
    /* Extra Light */
    font-style: normal;
}

.OurStory-header-left-title {
    margin: 0;
    color: #242424;
    font-size: 32px;
    font-family: 'EloquiaDisplay';
    font-weight: 600;
    word-wrap: break-word
}

.OurStory-header-left-text {
    margin: 0;
    color: #777777;
    font-size: 20px;
    font-family: 'EloquiaDisplay';
    font-weight: 400;
    line-height: 18px;
    word-wrap: break-word;
    width: 54%;
}

.OurStory-header-right-button-light {
    padding: 10px;
    background: white;
    border-radius: 20px;
    border: 1px #0070FF solid;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    width: 114px;
}

.OurStory-header-right-button-light div {
    color: #0070FF;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word
}

.OurStory-header-right-button-blue {
    padding: 10px;
    background: #0070FF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    border: 1px #0070FF solid;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    width: 114px;
}

.OurStory-header-right-button-blue div {
    color: white;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word
}

.OurStory-header-right {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 20px;

}

.OurStory-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 20px auto;
}


.OurStory-main {

    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.OurStory-main-right {
    background: white;
    border-radius: 14px;
    border: 1px #E5E5E5 solid;
    width: 912px;
    height: 947px;
    margin: 30px;

}

.OurStory {
    width: 80%;
    margin: auto;
}



/* Mobile Optimization Styles */
@media (max-width: 768px) {
    .OurStory {
        width: 90%;
    }

    .OurStory-header {
        flex-direction: column;
        align-items: flex-start;
        gap: 30px;
    }

    .OurStory-header-right {
        flex-direction: column;
        align-items: flex-start;
        width: 40%;
        gap: 0;
        margin: auto;
    }

    .OurStory-header-right-button-light,
    .OurStory-header-right-button-blue {
        width: 100%;
        margin-bottom: 10px;
    }

    .OurStory-main {
        flex-direction: column;
        align-items: center;
    }

    .OurStory-main-right {
        width: 100%;
        height: auto;
        margin: 20px 0;
        padding: 20px;
        height: 500px;
    }

    .OurStory-header-left-text {
        width: 100%;
    }

    .OurStory-header-left-title {
        font-size: 24px;
    }

    .OurStory-header-left-text {
        font-size: 16px;
    }

    .OurStory-header-right-button-light div,
    .OurStory-header-right-button-blue div {
        font-size: 14px;
    }
}



/* Mobile Optimization Styles */
@media (min-width: 768px) {
    .OurStory-main-right{
        margin-bottom: 150px;
    }
}