.BioOptimiserStart {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    height: 100vh;
}

.BioOptimiserStart-title {
    text-align: center;
}

.BioOptimiserStart-green {
    background: linear-gradient(117.96deg, #A6FFCA 0%, #35DEFA 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 32px;
    font-family: 'Eloquia Display', sans-serif;
    font-weight: 600;
    word-wrap: break-word;
}

.BioOptimiserStart-black {
    color: #242424;
    font-size: 32px;
    font-family: 'Eloquia Display', sans-serif;
    font-weight: 600;
    word-wrap: break-word;
}

.BioOptimiserStart-black-bold {
    color: #242424;
    font-size: 32px;
    font-family: 'Eloquia Display', sans-serif;
    font-weight: 500;
    word-wrap: break-word;
}

.BioOptimiserStart-button {
    width: 323px;
    outline: none;
    border: none;
    padding: 10px;
    background: #0070FF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    cursor: pointer;
}

.BioOptimiserStart-select {
    color: white;
    font-size: 12px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    word-wrap: break-word;
}

.BioOptimiserStart-subtext {
    color: #0070FF;
    font-size: 12px;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
    word-wrap: break-word;
}


