    height: 343px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    background: #FAFAFA;
    border-radius: 14px;
    border: 1px #E5E5E5 solid;
    gap: 10px;
    align-items: center;
    padding-left: 30px;
}

.BioOptimiser-music-section-right-item-left-title {
    margin: 0;
    color: black;
    font-size: 16px;
    font-family: Montserrat;
    font-weight: 400;
    word-wrap: break-word
}

.BioOptimiser-music-section-right-item-left-text {
    margin: 0;
    color: black;
    font-size: 32px;
    font-family: Lato;
    font-weight: 700;
    word-wrap: break-word
}

.BioOptimiser-music-section-right-item-footer-title {
    margin: 0;
    color: #D9D9D9;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word
}

.BioOptimiser-music-section-right-item-footer img {
    width: 35px;
    height: 25px;
}

.BioOptimiser-music-section-right-item {
    background: white;
    box-shadow: 0px 4px 7px 4px rgba(229, 229, 229, 0.50);
    border-radius: 14px;
    border: 1px #E5E5E5 solid;
    width: 374px;
    height: 126px;
    display: flex;
    flex-direction: row;
    position: relative;
    align-items: center;
    justify-content: space-around;
    gap: 50px;
}
.BioOptimiser-music-section-right-item-button {
    width: 374px;
    height: 126px;
    display: flex;
    flex-direction: row;
    position: relative;
    align-items: center;
    justify-content: space-around;
    gap: 50px;
}

.BioOptimiser-music-section-right-item-footer {
    position: absolute;
    bottom: 10px;
    right: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 2px;
}



.BioOptimiser-music-section-right-button {
    width: 195px;
    padding: 10px;
    background: #0070FF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    border: 1px #0070FF solid;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    cursor: pointer;
    margin: 0 auto;
    height: 30px;
}

.BioOptimiser-music-section-right-button div {
    color: white;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word
}



.BioOptimiser-music-section {
    width: 80%;
    margin: 50px auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

}




