.CompetitorComparaisonStep2-header-left-title {
    margin: 0;
    color: #242424;
    font-size: 32px;
    font-family: Eloquia Display;
    font-weight: 400;
    word-wrap: break-word
}


.CompetitorComparaisonStep2-header-left-title span {
    background: linear-gradient(90deg, #A7FFCA 0%, #22B7FD 50%, #5434FC 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Eloquia Display;
    font-weight: 600;
}

.CompetitorComparaisonStep2-header-left-text {
    margin: 0;
    color: #777777;
    font-size: 20px;
    font-family: Eloquia Display;
    font-weight: 600;
    word-wrap: break-word
}

.CompetitorComparaisonStep2-header-right button {
    padding: 10px;
    background: #0070FF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    border: 1px #0070FF solid;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    color: white;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word;
    width: 282px;
}

.CompetitorComparaisonStep2-header-right p {
    color: #0070FF;
    font-size: 12px;
    font-family: Lato;
    font-weight: 400;
    word-wrap: break-word;
    margin: 0;
    text-align: center;
}

.CompetitorComparaisonStep2-header-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.CompetitorComparaisonStep2-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    margin: 40px auto;
}



.CompetitorComparaisonStep2-item {
    width: 435px;
    height: 218px;
    background: #FAFAFA;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 14px;
    border: 1px #E5E5E5 solid
}

.CompetitorComparaisonStep2-items-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 1000px;
    margin: auto;
    gap: 50px;
}


.CompetitorComparaisonStep2-item-left {
    width: 139px;
    height: 139px;
    background: #D9D9D9;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 9999px;
    margin: auto 0;
}

.CompetitorComparaisonStep2-item {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 20px;
    position: relative;
}

.CompetitorComparaisonStep2-item-right-socials {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.CompetitorComparaisonStep2-item-right-socials-left {
    text-align: right;

}

.CompetitorComparaisonStep2-item-right-socials-right {
    text-align: left;

}


.CompetitorComparaisonStep2-item-right button {
    width: 194px;
    padding: 10px;
    background: #00A4FF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    border: 1px #00A4FF solid;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: inline-flex;
    color: white;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    word-wrap: break-word;
}


.CompetitorComparaisonStep2-item-right-title {
    margin: 0;
    color: black;
    font-size: 20px;
    font-family: Eloquia Display;
    font-weight: 600;
    word-wrap: break-word
}

.CompetitorComparaisonStep2-item-right-socials-left p {
    color: black;
    font-size: 20px;
    font-family: Eloquia Display;
    font-weight: 600;
    word-wrap: break-word;
    margin: 0;
}

.CompetitorComparaisonStep2-item-right-socials-right p {
    color: black;
    font-size: 20px;
    font-family: Eloquia Display;
    font-weight: 600;
    word-wrap: break-word;
    margin: 0;
}

.CompetitorComparaisonStep2-item-right {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}


.CompetitorComparaisonStep2-item-icon {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 41px;
    height: 41px;
    background: #D9D9D9;
    border-radius: 9999px
}

.CompetitorComparaisonStep2-item:hover {
    width: calc(435px*1.1);
    height: calc(218px*1.1);
    box-shadow: 0px 0px 10px rgba(0, 164, 255, 0.43);
    cursor: pointer;
}

.CompetitorComparaisonStep2-item-last {
    display: flex;
    justify-content: center;
    align-items: center;
}

.CompetitorComparaisonStep2-item-last p {
    margin: 0;
    color: #777777;
    font-size: 20px;
    font-family: Eloquia Display;
    font-weight: 600;
    word-wrap: break-word;
    width: 435px;
    text-align: center;
}



/* General Mobile Optimization */
@media screen and (max-width: 768px) {
    .CompetitorComparaisonStep2-header {
        flex-direction: column;
        width: 90%;
        margin: 20px auto;
        text-align: center;
        gap: 20px;
    }

    .CompetitorComparaisonStep2-header-left-title {
        font-size: 24px;
    }

    .CompetitorComparaisonStep2-header-left-text {
        font-size: 16px;
    }

    .CompetitorComparaisonStep2-header-right button {

        font-size: 14px;
    }

    .CompetitorComparaisonStep2-items-container {
        flex-direction: column;
        width: 80%;
        gap: 20px;
        padding: 10px;
    }

    .CompetitorComparaisonStep2-item {
        width: 90%;
        flex-direction: column;
        align-items: center;
        height: auto;
        padding: 20px;
    }

    .CompetitorComparaisonStep2-item-left {
        width: 100px;
        height: 100px;
    }

    .CompetitorComparaisonStep2-item-right {
        align-items: center;
        text-align: center;
        gap: 10px;
    }

    .CompetitorComparaisonStep2-item-right-title {
        font-size: 18px;
    }

    .CompetitorComparaisonStep2-item-right-socials {}

    .CompetitorComparaisonStep2-item-right-socials-left p,
    .CompetitorComparaisonStep2-item-right-socials-right p {
        font-size: 16px;
    }

    .CompetitorComparaisonStep2-item-right button {
        width: 100%;
        font-size: 14px;
    }

    .CompetitorComparaisonStep2-item-icon {
        width: 30px;
        height: 30px;
        top: 5px;
        right: 5px;
    }

    .CompetitorComparaisonStep2-item-last p {
        font-size: 16px;
        width: 90%;
    }

}


/* Mobile Optimization Styles */
@media (max-width: 768px) {

    .CompetitorComparaisonStep2-header {
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }

    .CompetitorComparaisonStep2-header-left-title {
        font-size: 24px;
    }

    .CompetitorComparaisonStep2-header-left-text {
        font-size: 16px;
    }

    .CompetitorComparaisonStep2-header-right {
        width: 100%;
        align-items: center;
    }

    .CompetitorComparaisonStep2-header-right button {
        width: 100%;
        font-size: 14px;
    }

    .CompetitorComparaisonStep2-header-right p {
        font-size: 14px;
    }
}


@media (min-width: 768px) {

    .CompetitorComparaisonStep2 {
        margin-bottom: 100px;
    }
}